import React, {lazy, Suspense} from 'react';
import { Hidden } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';

const Contact = lazy(() => import('../components/contact/Contact'));
const Content = lazy(() => import('../components/content/Content'));
const DisplacementSphere = lazy(() => import('../components/background/DisplacementSphere'));
const ThemeToggle = lazy(() => import('../components/theme/ThemeToggle'));
const Resume = lazy(() => import('../components/resume/Resume'));
const SocialIcons = lazy(() => import('../components/content/SocialIcons'));
const SpeedDials = lazy(() => import('../components/speedDial/SpeedDial'));
const SideNavbar = lazy(() => import('../components/nav/SideNavbar'));
const Works = lazy(() => import('../components/works/Works'));
const About = lazy(() => import('../components/about/About'));
const useStyles = makeStyles(() => ({
  root: {
    display: 'flex',
    flexDirection: 'column',
    minHeight: '100vh',
  },
}));


export const Home = () => {
  const classes = useStyles();
  return (
      <>
      <Suspense fallback={<div>Loading...</div>}>
      <div className={classes.root} id="home">
        <DisplacementSphere />
        <Content />
        <ThemeToggle />
        <Hidden smDown>
          <SocialIcons />
        </Hidden>
        <Hidden mdUp>
          <SpeedDials />
        </Hidden>
        <Resume />
      </div>
        <SideNavbar />
        <About />
        <Works />
      {<Contact />}
      </Suspense>

      </>
  );
};
